@import url(./vendor/normalize.css);
@import url(./vendor/fonts.css);


:root {
  --promo-color: rgba(243, 193, 248);

  --text-color: black;
  --text-accent: white;
  --text-grey: #a0a0a0;
  --text-error: #FF4062;

  --button-color: black;
  --button-accent: #2BE080;
  --button-grey: #f9f9f9;
  --font-Inter: 'Inter', Arial, Helvetica, sans-serif;
}

html {
  scroll-behavior: smooth;
}

.body {
  background-color: #e5e5e5;
}

.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  justify-content: space-between;
}

.page {
  min-width: 320px;
  max-width: 1280px;
  min-height: 100vh;
  font-family: var(--font-Inter);
  color: var(--text-color);
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: grayscale;
  margin: 0 auto;
  box-sizing: border-box;
  width: 100%;
  background-color: white;
  position: relative;
}

.section {
  padding: 40px 70px 75px 70px;
  box-sizing: border-box;
}

.hover-link:hover {
  border-bottom: 5px solid var(--button-accent);
  transition: all .2s ease-in;
}

.hover-button:hover {
  background-color: var(--button-accent);
  color: var(--text-color);
  box-shadow: 2px 2px 2px 2px var(--promo-color);
  border: 0;
  transition: all .3s ease-in;
}

.hover-opacity:hover {
  opacity: 0.6;
  transition: opacity .3s ease-in;
}

.link {
  text-decoration: none;
  color: var(--text-color);
}

.link_selected {
  border-bottom: 2px solid var(--text-color);
}

@media screen and (max-width: 960px) {
  .section {
    padding: 40px;

  }

}

@media screen and (max-width: 568px) {
  .section {
    padding: 16px;

  }

}

.section__header {
  width: 100%;
  border-bottom: 1px solid var(--button-color);
  line-height: 1.2;
  font-weight: 400;
  font-size: 22px;
  padding: 0 0 25px 0;
  margin: 50px 0;
  letter-spacing: -.06rem;
}


@media screen and (max-width: 568px) {
  .section__header {
    font-size: 18px;

  }

}